.containerLabel{
    display: flex;
    gap: 0.5rem;
    align-items: stretch;
}
.containerLabel > p{
    font-weight: 700;
    color: #060d18;
}
.containerLabel > span{
    background-color:#0c2e45;

    padding: 0.04rem 0.085rem ;
    width: 1rem;
    height: 1rem;

    line-height: 1;
    vertical-align: middle;
    text-align: center;

    border-radius: 4.8px;

    font-weight: 700;
}