.task{
    display: flex;
    justify-content: space-between;
    padding: 0.8rem;
    align-items: flex-start;
    gap: 0.5rem;
    
    margin-bottom: 0.5rem;
    
    background-color: rgb(107, 107, 107);

    border-radius: 4px;
}
.task > p{
    width: 93%;
    
    text-align: justify;

    line-height: 0.9;
    vertical-align: sub;
    align-self: flex-start;
}
.task > input{
    border-radius: 20px;
    align-self: flex-start;
    clip-path: circle(46% at 50% 50%); 
}
.task > input:checked{

}
.task > svg{
    width: 1rem;
    height: 0.9rem;

    line-height: 0.9;
    align-self: flex-start;

    cursor: pointer;

}